import { Link } from "react-router-dom";

export const AuthForm = ({ props }) => {

    const otherAuthLink = props.authType == 'Connexion' ? 'Inscription' : 'Connexion';
    const otherAuthMsg2 = props.authType == 'Connexion' ? "Inscrivez vous !" : "Connectez vous !";
    const otherAuthMsg1 = props.authType == 'Connexion' ? "Vous n'avez pas encore de compte ?" : "Vous avez deja un compte ?";

    return (<div className="connexionPart">
        <div className="cpBody">
            <section className="cpbHeader">
                <img src={require("../Assets/images/logo.webp").default} />
                <hr />
                <span>Mini-espace etudiant</span>
            </section>

            <section className="cpbAuth">
                <h2>{props.authType}</h2>
                <form className="connexionForm">
                    {props.authFields}
                </form>
                <div className="otherAuth">
                    <p>{otherAuthMsg1} </p>
                    <Link to={"/" + otherAuthLink}>{otherAuthMsg2}</Link>
                </div>
            </section>
        </div>
    </div>)
}

export const AuthFormConnexion = () => {
    return (<>
        <div className="formSegment">
            <label>Pseudo ou Mail</label>
            <section>
                <input type="text" placeholder="Jhon,john@gmail.com..." />
                <i className="mdi mdi-account"></i>
            </section>
        </div>
        <div className="formSegment">
            <label>Mot de passe</label>
            <section>
                <input type="password" placeholder="MonMotDePasse" />
                <i className="mdi mdi-account"></i>
            </section>
        </div>
        <div className="formBtn">
            <button>Connexion</button>
        </div>
    </>)
}

export const AuthFormInscription = () => {
    return (<>
        <div className="formStepIllustration">
            <img src={require("../Assets/images/illustrations/identité.svg").default} />
            <img src={require("../Assets/images/illustrations/personne.svg").default} />
            <img src={require("../Assets/images/illustrations/scolarité1.svg").default} />
            <img src={require("../Assets/images/illustrations/mail.svg").default} />
        </div>
        <div className="formSegment">
            <label>Pseudo</label>
            <section>
                <input type="text" placeholder="Jhon,Jeanne..." />
                <i className="mdi mdi-account"></i>
            </section>
        </div>
        <div className="formSegment">
            <label>Mail</label>
            <section>
                <input type="mail" placeholder="monmail@gmail.com..." />
                <i className="mdi mdi-account"></i>
            </section>
        </div>
        <div className="formBtn">
            <button>Inscription</button>
        </div>
    </>)
}