import { Link } from "react-router-dom";
import "../Assets/styles/accueil.css"
const Accueil = () => {
    return <div className="homePart">
        <img src={require("../Assets/images/logo.webp").default} />
        <h1>Bienvenue sur Univearn</h1>
        <section>
            <Link class="button-anim1" to="/Inscription">Rejoins l'aventure</Link>
            <Link class="button-anim1" to="/Connexion">Connexion</Link>
        </section>
    </div>
}

export default Accueil;